  font-display: swap;
}
/* fallback */
@font-face {
  font-family: "Material Symbols Outlined";
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialsymbolsoutlined/v166/kJF1BvYX7BgnkSrUwT8OhrdQw4oELdPIeeII9v6oDMzByHX9rA6RzaxHMPdY43zj-jCxv3fzvRNU22ZXGJpEpjC_1v-p_4MrImHCIJIZrDCvHOej.woff2) format("woff2");
}
.material-symbols-outlined, .icon, i {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 24;
  font-family: "Material Symbols Outlined";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: "liga";
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ul li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration-color: rgba(var(--accent-r), var(--accent-g), var(--accent-b), 1);
}

::selection {
  background: rgba(var(--accent-r), var(--accent-g), var(--accent-b), 1);
  color: #070707;
}

img {
  display: block;
  max-width: 100%;
}

body {
  background: #141414;
  color: #c7c7c7;
  font-family: "Roboto Slab", serif;
  height: 100vh;
  height: 100dvh;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent;
}

.game {
  height: 100%;
  display: flex;
}
@media screen and (max-width: 63.9375em) {
  .game {
    padding: 100px 10% 100px;
  }
}
.game-title {
  font-size: 49px;
  margin-bottom: 42px;
}
@media screen and (max-width: 63.9375em) {
  .game-title {
    font-size: 35px;
    margin-bottom: 14px;
  }
}
.game p {
  margin-bottom: 50px;
  font-size: 18px;
  opacity: 0.63;
}
.game-sidebar {
  overflow: auto;
  z-index: 10;
}
@media print, screen and (min-width: 64em) {
  .game-sidebar {
    top: 0;
    left: 0;
    width: 490px;
    padding: 2.625rem;
    height: 100%;
    background: #070707;
    box-shadow: inset 0 0 0 0.875rem #141414;
    border-radius: 2.625rem;
  }
}
@media screen and (max-width: 63.9375em) {
  .game-sidebar {
    position: unset;
  }
}
@media screen and (max-width: 63.9375em) {
  .game-sidebar .game-title-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #070707;
    overflow: auto;
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    height: auto;
  }
  .game-sidebar .game-title-container p {
    margin: 0;
    text-align: center;
  }
}
@media screen and (max-width: 63.9375em) {
  .game-container {
    width: 100%;
    margin: 50px auto;
    max-width: 512px;
  }
}
@media print, screen and (min-width: 64em) {
  .game-container {
    position: fixed;
    width: calc(100% - 490px);
    left: 490px;
    padding: 100px 0;
  }
}
.game-container .grid-outer {
  width: 100%;
  max-width: 32rem;
  margin: 0 auto;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 63.9375em) {
  .game-container .grid-outer {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
  }
}
.game-container .grid-outer .grid-container {
  width: 32rem;
  height: 32rem;
  display: flex;
  flex-wrap: wrap;
}
@media screen and (max-width: 63.9375em) {
  .game-container .grid-outer .grid-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}
.game-container .grid-outer .grid-container .grid-cell {
  width: calc(100% / var(--cells));
  height: calc(100% / var(--cells));
  background-color: transparent;
  overflow: hidden;
  position: relative;
}
.game-container .grid-outer .grid-container .grid-cell:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px dotted rgba(80, 80, 80, 0.2);
}
.game-container .grid-outer .grid-container .grid-cell.middle.h:after {
  border-bottom: 1px solid rgba(80, 80, 80, 0.5);
}
.game-container .grid-outer .grid-container .grid-cell.middle.v:after {
  border-right: 1px solid rgba(80, 80, 80, 0.5);
}
.game-container .grid-outer .grid-container .grid-cell.quart.h:after {
  border-bottom: 1px solid rgba(80, 80, 80, 0.5);
}
.game-container .grid-outer .grid-container .grid-cell.quart.v:after {
  border-right: 1px solid rgba(80, 80, 80, 0.5);
}
.game-container .grid-outer .grid-container.mirror-h-active:before {
  content: "";
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: rotate(180deg);
  border: 1px solid red;
  z-index: 1;
}
.game-container .grid-outer .grid-container.mirror-v-active:after {
  content: "";
  height: 0;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0%;
  transform: rotate(180deg);
  border: 1px solid red;
  z-index: 1;
}

@media screen and (max-width: 63.9375em) {
  .game-options {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #070707;
    transform: translateY(calc(100% - 52px));
    transition: all 0.25s ease-out;
    height: auto;
    max-height: 70vh;
    max-height: 70dvh;
    overflow: hidden;
    z-index: 100;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "title title" "size background" "color color";
  }
  .game-options.opened {
    transform: translateY(0);
    overflow: auto;
  }
}
@media screen and (max-width: 47.9375em) {
  .game-options {
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas: "title" "size" "background" "color";
  }
}
.game-options .option-category-title {
  margin-bottom: 20px;
}
@media screen and (max-width: 63.9375em) {
  .game-options [class*=game-options] {
    padding: 20px 40px;
  }
}
.game-options-grid-size {
  margin-bottom: 49px;
}
.game-options-grid-size-label {
  display: block;
  margin-bottom: 0.875rem;
}
@media screen and (max-width: 63.9375em) {
  .game-options-grid-size {
    grid-area: size;
  }
}
.game-options-grid-size label {
  display: grid;
  grid-template-columns: auto 8.75rem;
  gap: 2.1875rem;
}
.game-options-grid-size .action-btn {
  flex-shrink: 0;
}
.game-options-background {
  margin-bottom: 49px;
}
@media screen and (max-width: 63.9375em) {
  .game-options-background {
    grid-area: background;
  }
}
.game-options-background .background-list {
  display: flex;
  gap: 28px;
}
.game-options-background .background-list-item {
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.game-options-background .background-list-item button {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  outline: none;
  box-shadow: 0px 0px 0px 2px rgba(199, 199, 199, 0.14);
  cursor: pointer;
}
.game-options-background .background-list-item button.selected {
  opacity: 1;
  box-shadow: 0px 0px 0px 5px rgba(199, 199, 199, 0.77);
}
.game-options-background .background-list-item button span {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
  color: #c7c7c7;
  padding: 7px 0;
  text-align: center;
}
.game-options-background .background-list-item button.white {
  background: #fff;
}
.game-options-background .background-list-item button.black {
  background: #000;
}
.game-options-background .background-list-item button.transparent {
  background: transparent;
  background-image: linear-gradient(90deg, rgba(199, 199, 199, 0.14) 49%, transparent 50%), linear-gradient(0deg, rgba(199, 199, 199, 0.14) 49%, transparent 50%);
  background-size: 14px 14px;
}
@media screen and (max-width: 63.9375em) {
  .game-options-pencil-color {
    grid-area: color;
  }
}
.game-options-pencil-color .color-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1.3125rem;
}
.game-options-pencil-color .color-list-item {
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 10px;
  position: relative;
}
.game-options-pencil-color .color-list-item button,
.game-options-pencil-color .color-list-item label {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: none;
  outline: none;
  box-shadow: 0px 0px 0px 2px rgba(199, 199, 199, 0.14);
  cursor: pointer;
}
.game-options-pencil-color .color-list-item button.selected,
.game-options-pencil-color .color-list-item label.selected {
  opacity: 1;
  box-shadow: 0px 0px 0px 5px rgba(199, 199, 199, 0.77);
}
.game-options-pencil-color .color-list-item button.white,
.game-options-pencil-color .color-list-item label.white {
  background: #ffffff;
}
.game-options-pencil-color .color-list-item button.black,
.game-options-pencil-color .color-list-item label.black {
  background: #000000;
}
.game-options-pencil-color .color-list-item button.red,
.game-options-pencil-color .color-list-item label.red {
  background: #c62828;
}
.game-options-pencil-color .color-list-item button.orange,
.game-options-pencil-color .color-list-item label.orange {
  background: #f57c00;
}
.game-options-pencil-color .color-list-item button.yellow,
.game-options-pencil-color .color-list-item label.yellow {
  background: #fdd835;
}
.game-options-pencil-color .color-list-item button.green,
.game-options-pencil-color .color-list-item label.green {
  background: #8bc34a;
}
.game-options-pencil-color .color-list-item button.blue,
.game-options-pencil-color .color-list-item label.blue {
  background: #1976d2;
}
.game-options-pencil-color .color-list-item button.indigo,
.game-options-pencil-color .color-list-item label.indigo {
  background: #ab47bc;
}
.game-options-pencil-color .color-list-item button.violet,
.game-options-pencil-color .color-list-item label.violet {
  background: #673ab7;
}
.game-options-pencil-color .color-list-item button.clear,
.game-options-pencil-color .color-list-item label.clear {
  background: linear-gradient(90deg, rgba(199, 199, 199, 0.14) 49%, transparent 50%), linear-gradient(0deg, rgba(199, 199, 199, 0.14) 49%, transparent 50%);
}
.game-options-pencil-color .color-list-item button.clear .icon,
.game-options-pencil-color .color-list-item label.clear .icon {
  color: #c7c7c7;
}
.game-options-pencil-color .color-list-item button span,
.game-options-pencil-color .color-list-item label span {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
  color: #c7c7c7;
  padding: 7px 0;
  text-align: center;
  font-family: "Roboto Slab";
  z-index: 1;
  font-size: 0.875rem;
}
.game-options-pencil-color .color-list .custom-picker {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.game-options-pencil-color .color-list .custom-picker label {
  overflow: hidden;
  position: relative;
}
.game-options-pencil-color .color-list .custom-picker label.selected {
  box-shadow: 0px 0px 0px 5px rgba(199, 199, 199, 0.77);
}
.game-options-pencil-color .color-list .custom-picker span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0;
  background: conic-gradient(rgb(255, 0, 0) 0%, rgb(255, 154, 0) 10%, rgb(208, 222, 33) 20%, rgb(79, 220, 74) 30%, rgb(63, 218, 216) 40%, rgb(47, 201, 226) 50%, rgb(28, 127, 238) 60%, rgb(95, 21, 242) 70%, rgb(186, 12, 248) 80%, rgb(251, 7, 217) 90%, rgb(255, 0, 0) 100%);
  z-index: 2;
  width: 1.3125rem;
  height: 1.3125rem;
  box-shadow: 0 0 0 0.25rem #c7c7c7;
  border-radius: 50%;
}
.game-options-pencil-color .color-list .custom-picker input[type=color] {
  position: absolute;
  width: 220%;
  height: 220%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: none;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
}

.actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.875rem;
  max-width: 32rem;
  margin: 2.1875rem auto;
}
@media screen and (max-width: 500px) {
  .actions {
    max-width: 18.75rem;
  }
}
@media screen and (max-width: 47.9375em) {
  .actions {
    margin: 1.3125rem auto 2.1875rem;
  }
}
.actions .action-btn {
  width: 3.5rem;
  height: 3.5rem;
}
.actions .available-undo {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.4375rem;
  display: block;
  line-height: 1;
  font-size: 0.75rem;
  opacity: 0.77;
}
.actions .action-btn.active {
  background: #c7c7c7;
  color: #070707;
}
.actions .mirror-h .icon,
.actions .mirror-v .icon {
  position: relative;
  width: 1.25rem;
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: 1.3125rem;
  box-shadow: inset 0 0 0 0.125rem rgba(199, 199, 199, 0.14);
  border-radius: 0.25rem;
}
.actions .mirror-h .icon:after,
.actions .mirror-v .icon:after {
  content: "";
  display: block;
  width: 0;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 0.0625rem #c7c7c7;
}
.actions .mirror-h.active .icon,
.actions .mirror-v.active .icon {
  box-shadow: inset 0 0 0 0.125rem rgba(7, 7, 7, 0.28);
}
.actions .mirror-h.active .icon:after,
.actions .mirror-v.active .icon:after {
  box-shadow: 0 0 0 0.0625rem #070707;
}
.actions .mirror-v .icon:after {
  width: 100%;
  height: 0;
}

.action-btn {
  border: none;
  height: 42px;
  border-radius: 8px;
  font-family: inherit;
  font-weight: 700;
  background: transparent;
  color: #c7c7c7;
  box-shadow: inset 0 0 0 0.1875rem rgba(199, 199, 199, 0.14);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  padding: 0.4375rem 0.875rem;
  gap: 0.625rem;
  transform: scale(1);
  transition: all 0.07s ease-out;
}
.action-btn:not([disabled]):hover {
  transform: scale(1.07);
}
.action-btn:not([disabled]):active {
  transform: scale(1.05);
}
.action-btn[disabled] {
  cursor: default;
  opacity: 0.28;
}
@media screen and (max-width: 63.9375em) {
  .action-btn {
    margin: 0;
    padding: 0.625rem;
  }
}
.action-btn .icon {
  font-size: 1.125rem;
}

#grid {
  box-shadow: 0 0 0 7px rgba(199, 199, 199, 0.14);
}

#grid.no-guides .grid-cell:after {
  border: none;
}

[data-tooltip] {
  position: relative;
}
[data-tooltip]:after {
  content: attr(data-tooltip);
  position: absolute;
  padding: 0.4375rem 0.875rem;
  display: block;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #070707;
  color: #c7c7c7;
  border-radius: 0.5rem;
  box-shadow: inset 0 0 0 0.125rem rgba(199, 199, 199, 0.14);
  transition: all 0.21s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  pointer-events: none;
  opacity: 0;
  z-index: -1;
}
@media screen and (min-width: 75em) {
  [data-tooltip]:hover:after {
    transform: translate(-50%, -110%);
    opacity: 1;
  }
}

#exportDrawing {
  background: #3b9d5f;
  color: #070707;
}

#clearDrawing {
  background: #ef2626;
  color: #070707;
}

[type=range] {
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  margin: 14px 0;
  width: 100%;
}
[type=range]::-moz-focus-outer {
  border: 0;
}
[type=range]:focus {
  outline: 0;
}
[type=range]:focus::-webkit-slider-runnable-track {
  background: rgba(212, 212, 212, 0.21);
}
[type=range]:focus::-ms-fill-lower {
  background: rgba(199, 199, 199, 0.21);
}
[type=range]:focus::-ms-fill-upper {
  background: rgba(212, 212, 212, 0.21);
}
[type=range]::-webkit-slider-runnable-track {
  cursor: default;
  height: 14px;
  transition: all 0.2s ease;
  width: 100%;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2), 0 0 2px rgba(13, 13, 13, 0.2);
  background: rgba(199, 199, 199, 0.21);
  border: 2px solid rgba(199, 199, 199, 0.21);
  border-radius: 5px;
}
[type=range]::-webkit-slider-thumb {
  box-shadow: 7px 7px 7px rgba(0, 0, 0, 0.2), 0 0 7px rgba(13, 13, 13, 0.2);
  background: #070707;
  border: 2px solid #c7c7c7;
  border-radius: 14px;
  box-sizing: border-box;
  cursor: default;
  height: 28px;
  width: 28px;
  -webkit-appearance: none;
  margin-top: -9px;
}
[type=range]::-moz-range-track {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2), 0 0 2px rgba(13, 13, 13, 0.2);
  cursor: default;
  height: 14px;
  transition: all 0.2s ease;
  width: 100%;
  background: rgba(199, 199, 199, 0.21);
  border: 2px solid rgba(199, 199, 199, 0.21);
  border-radius: 5px;
  height: 7px;
}
[type=range]::-moz-range-thumb {
  box-shadow: 7px 7px 7px rgba(0, 0, 0, 0.2), 0 0 7px rgba(13, 13, 13, 0.2);
  background: #070707;
